.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  margin-bottom: 20px;
  padding: 0 20px;
  background-color: #151515;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
}
.wrapper {
  position: relative;
  margin-bottom: 20px;
  z-index: 20;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
