.wrapper {
  display: flex;
  justify-content: center;
}
.formWrapper {
  position: fixed;
  bottom: 0;
  height: 76px;
  width: 100%;
  background-color: #252525;
  padding: 16px 20px;
  margin: 0 auto;
  max-width: 400px;
}
.messages {
  margin-bottom: 76px;
}
.form {
  display: flex;
  align-items: center;
}
.input {
  width: 100%;
  height: 44px;
  background-color: #fff;
  border: none;
  outline: none;
  padding: 15px;
  border-bottom-left-radius: 22px;
  border-top-left-radius: 22px;
}
.send {
  height: 44px;
  display: flex;
  align-items: center;
  background-color: #fff;
  border-bottom-right-radius: 22px;
  border-top-right-radius: 22px;
  padding: 0 10px;
}
.send:hover {
  background: rgb(194, 194, 194);
}
.send svg {
  fill: #383838;
}

.messagesWrapper {
  display: flex;
  flex-direction: column;
  border-radius: 10px;
}
.messageContainer {
  display: flex;
}
.message {
  background-color: #1e90ff;
  margin-bottom: 10px;
  padding: 5px 10px;
  border-radius: 10px;
  min-width: 8vh;
  max-width: 70%;
  word-break: break-all;
  color: #fff;
  display: flex;
  flex-direction: column;
  margin-right: 5px;
}
.messageDate {
  color: #ccc;
  font-size: 9px;
}
.noMessages {
  display: flex;
  justify-content: center;
  align-items: center;
}
